<template>
  <div>
    <article-item v-for="item in articleList" :key="+item.id" :art="item" />
    <div>
      <paging :curPage="curPage" :pageSize="pageSize" :total="+total" :toPage="getData" />
    </div>
  </div>
</template>

<script>
import ArticleItem from './ArticleItem.vue';
import Paging from './Paging.vue';
import { getArticle } from '../api';

export default {
  data() {
    return {
      total: 300, // 
      curPage: 1, // 
      count: 5, // 
      limit: 5, // 
      pageSize: 5, // 
      articleList: [
        {
          id: Number,
          title: String,
          date: String,
          content: String,
          views: Number,
          tags: Array,
        },
      ],
    };
  },
  components: {
    ArticleItem,
    Paging,
  },
  methods: {
    async getData(page, pageSize) {
      const result = await getArticle(page, pageSize);
      console.log(result);
      this.articleList = JSON.parse(result.data);
      this.total = result.count;
      this.count = Math.ceil(this.total / this.pageSize);
      this.curPage = page;
    },
  },
  async created() {
    this.getData(this.curPage, this.pageSize);
  },
};
</script>

