<template>
  <!-- blog content -->
  <div class="art-body" id="article">
    <span class="day">:</span>
    <span class="day">
      <a href></a>
    </span> >>
    <span class="day">
      <a href></a>
    </span> >> 
    <div class="title">{{title}}</div>
    <div class="info">
      <span class="day">: </span>
      <span class="day">
        
        <a href>{{tags}}</a>
      </span>
      <span class="day">{{ctime}}</span>
      <span class="day"> {{views}}</span>
      <span class="day">
        
        <a href>1</a>
      </span>
    </div>
    <hr />
    <div class="main-body" v-html="content">234</div>
  </div>
</template>

<script>
import { getArt } from '../api';

export default {
  data() {
    return {
      title: '',
      content: '',
      ctime: '',
      tags: '',
      views: '',
    };
  },
  async created() {
    const result = await getArt(this.$route.params.id);
    const artObj = JSON.parse(result.data);
    this.title = artObj.title;
    this.content = artObj.content;
    this.ctime = artObj.ctime;
    this.tags = artObj.tags;
    this.views = artObj.views;
  },
};
</script>

<style lang="scss" scoped>
.art-body {
  width: calc(100% - 40);
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 8px rgba(97, 97, 97, 0.8);
  padding: 20px;
  .title {
    padding: 20px 0px;
    font-size: 18px;
  }
  .info {
    padding: 10px 0px;
    font-size: 12px;
  }
  .main-body {
    padding: 10px 10px;
  }
}
</style>
